/* styles go here */
body {
  font-family: Roboto, "Times New Roman", sans-serif;
  margin: 0;
  padding: 0 50px;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.nav__list {
  display: flex;
  margin: 0;
}

.nav__item {
  list-style-type: none;
}

.nav__link {
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  margin-right: 20px;
  height: 60px;
  display: flex;
  align-items: center;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
}

.is-active::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  border-radius: 8px;
  background: #00acdc;
  width: 37px;
  height: 4px;
}
